.driver__header__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 10px;
    background-color: rgb(227, 215, 54);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    box-sizing: border-box;
  }

  .dark .driver__header__content {
    background-color: rgb(89, 86, 48);
  }
  
  .driver__header__info {
    font-size: 20px;
    text-transform: uppercase;
  }
  
  .driver__header__info span{
    font-weight: bold;
  }
  
  .driver__header__btn {
    display: inline-block;
    padding: 10px 20px;
    font-size: 14px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    text-transform: uppercase;
    cursor: pointer;
  }
  
  .driver__header__btn:hover {
    background-color: #0056b3;
  }
  
  .driver__header__btn:active {
    background-color: #003080;
  }