.users__modal-elem-wrap {
    border-bottom: 1px solid black;
    padding-bottom: 5px;
}

.usersModal__search-wrap {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid black;
    padding: 5px 0;
}

.usersModal__search-period{
    display: flex;
    gap: 1rem;
}

.usersModal__search-date{
    cursor: pointer;
}

.usersModal__search-input {
    width: 200px;
}



.usersModal__search-btns {
    display: flex;
    gap: 10px;
}

.usersModal__search-btn {
    padding-left: 5px;
    padding-right: 5px;
}

@media only screen and (max-width: 600px) { 
    .usersModal__search-input {
        width: 100px;
    }
}