.header__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    margin-bottom: 70px;
}

.logo {
  font-size: 40px;
  cursor: pointer;
}

.logoImg {
  width: 150px;
}

.header-btn {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    padding: 8px 14px;
    border-radius: 7px;
    text-transform: uppercase;
    background-color: #0C8EC8;    
    color: #fff;
    cursor: pointer;
}

