.modal-wrapper{
  position: fixed;
  top: 0;
  left: 0;
  width: 100dvw;
  height: 100dvh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #0000003b;
}

.modal-wrapper .login-form{
  position: relative;
}

.login-form {
    /* position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); */
    box-shadow: 0px 1px 3px 2px lightgray;
    background-color: #fff;
    padding: 24px 24px 12px 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .login-form-sms-req {
    margin: 0;
  }
  .login-form-sms-req-selected {
    margin: 0;
    color: #007bff;
    cursor: pointer;
  }
  
  .login-input {
    margin-bottom: 10px;
    padding: 5px;
    width: 200px;
    font-size: 16px;
    border: 1px solid black;    
  }
  .login-input:focus {
    outline: 1px solid #0C8EC8;
    border: none;
  }
  .login-form-sms-wrap {
    max-width: 200px;
  } 
  .login-form-sms-info {
    text-align: center;
    margin-bottom: 10px;
    margin-top: 0;
  }

  .login_btn-addWrap {
    width: 200px;
  }
  .login_btn-addText {
    font-size: 12px;
    margin: 0;
  }

  .login_btn-addSpan {
    color: #0000FF;
    cursor: pointer;
  }
  
  .login__save-btn {
    padding: 10px 20px;
    background-color: #0C8EC8;    
    color: #fff;  
    border: none;
    cursor: pointer;
    font-size: 16px;
  }
  .login_btn-addWrap input {
    cursor: pointer;
  }

  .login__save-info {
    font-size: 16px;
    text-align: center;
  }
  
  .login__save-btn:hover {
    background-color: #0e7fb3;
  }

.login__ask-span {
  color: gray;
  cursor: pointer;
}
  
.login__error-text {
  margin: 0;
  font-size: 14px;
  color: red;
  text-align: center;
  max-width: 200px;

}

.header_points-info {
  font-weight: 400;
  font-size: 20px;
  line-height: 32px;
  padding: 8px 14px;
  border-radius: 7px;
  text-transform: uppercase;
  background-color: #0C8EC8;
  color: #fff;
  cursor: pointer;
}

.height400 {
  height: 400px;
}

.modal__points {
  position: relative;
  display: flex;
  flex-direction: column;
  position: absolute;    
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 600px;  
  border-radius: 10px;
  box-shadow: 0px 1px 3px 2px lightgray;
  background-color: #fff;
  padding: 10px;
  overflow-y: auto;
}
.modal__points-header-info {
  background-color: #0C8EC8;  
  padding: 8px;
  border-radius: 5px;
}
.modal__points-content {
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 18px;
}
.modal__points-header-info p {
  margin: 0;
  text-transform: uppercase;
  font-size: 20px;
  text-align: center;
  color: #fff;
}
.modal__points-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.modal__points-header-close{
  background: url(../../images/close.png)
    no-repeat center;
    background-size: cover;   
    height: 20px;
    width: 20px;
    cursor: pointer;
}

.header_btns-wrap {
  display: flex;
  gap: 10px;
}

.page__privacyPolicy-header {
  margin: 0;
  margin-bottom: 20px;
}

.page__privacyPolicy-btn {
  display: flex;
  justify-content: center;
  font-weight: 400;
  font-size: 20px;
  line-height: 32px;
  padding: 8px 14px;
  border-radius: 7px;
  text-transform: uppercase;
  background-color: #0C8EC8;    
  color: #fff;
  cursor: pointer;
}

.modal__points-date {
  text-align: center;
  margin: 0;
}
.modal__points-cancel-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  box-shadow: 0px 2px 2px 2px rgb(192, 192, 192);
  cursor: pointer;
}
.modal__points-cancel-btn-text {
  margin-top: 5px;
  margin-bottom: 5px;
}

.noCursorPointer {
  pointer-events: none;
}

.no-reserved-trips-info {
  text-align: center;
  font-size: 18px;
  color: gray;
  margin-top: 10px;
  margin-bottom: 10px;
}

.hiddenAdapt {
  display: none;
}

.open__modal-navbar {
  background: url(../../images/menu.png)
  no-repeat center;
  background-size: cover;   
  height: 25px;
  width: 25px;
  cursor: pointer;
}

.modal__navbar {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: absolute;    
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  max-width: 600px;
  height: 400px;
  border-radius: 10px;
  box-shadow: 0px 1px 3px 2px lightgray;
  background-color: #fff;
  padding: 10px;
  overflow-y: auto;
}

.header_points-info-mob {
  text-align: center;
}

.hiddenLogin {
  display: none;
}

.modal__navbar-close {
  align-self: center;
  background: url(../../images/close.png)
  no-repeat center;
  background-size: cover;   
  height: 20px;
  width: 20px;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .shownLogin {
    display: block;
  }
  .hidden {
    display: none;
  }
  .shown {
    display: block;
  }
  .page__privacyPolicy-header {
    font-size: 16px;
  }
  .page__privacyPolicy-btn {
    font-size: 14px;
  }  

  .modal__points-content {    
    font-size: 14px;
  }
  .modal__points-header-info p {
    font-size: 16px;    
  }
  .modal__points-header {
    gap: 10px;
  }
  .logoImg {
    width: 110px;
  }  

  .header__content {
    margin-bottom: 0px;
  }
  .header_points-info {
    font-weight: 400;
    font-size: 14px;
    line-height: 32px;
    padding: 4px 7px;
    border-radius: 7px;
    text-transform: uppercase;
    background-color: #0C8EC8;
    color: #fff; 
    cursor: pointer;  
  }  
  .header-btn {
    font-weight: 400;
    font-size: 14px;
    line-height: 32px;
    padding: 4px 7px;
    border-radius: 7px;
    text-transform: uppercase;
    background-color: #0C8EC8;
    color: #fff;  
    cursor: pointer;
}
.logo{
  font-size: 30px;
}


}