.reservationTickets__card {
    display: flex;
    flex-direction: column;
    box-shadow: 0px 2px 2px 2px rgb(192, 192, 192);
    margin-bottom: 20px;    
    border-radius: 5px;
    padding: 10px;
    font-size: 22px;
}

.reservationTickets__card-details {
    border-top: 1px solid rgb(192, 192, 192);
}

.reserve-btn {
    max-width: 300px;
    font-size: 18px;
    line-height: 32px;
    padding: 6px 6px;
    border-radius: 7px;
    text-transform: uppercase;
    color: rgb(55, 55, 55);
    background-color: #0C8EC8;
    color: #fff;
    border: none;
    margin-top: 10px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.detail__form-reserve {
    display: flex;
    flex-direction: column;
}

.detail__form-input {
    margin-top: 10px;
    width: 250px;    
    height: 30px;
    font-size: 16px;
}

.reserve-btn:active {
    background-color: #0a6d9b;
    box-shadow: 0px 1px 5px 2px lightgray;
}

.form_submit {
    display: flex;
    flex-direction: column;
}

.detail__form-textarea {
    margin-top: 10px;
    width: 250px;    
    height: 40px;
    font-size: 18px;
    border: none;
}

.reservationTickets__card-disabled {
    pointer-events: none;
    opacity: 0.5;
}
.detail-row-selected {
    font-weight: 500;
}

.select_payment-item_wrap {
    display: flex;
    align-items: center;
    gap: 5px;
}
.select_payment-item_text {
    margin: 0;
}

.select_payment-wrap {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.select_payment-item {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 20px;
    background-color: #eee;
    background: #eee;
    border: 1px solid #ccc;    
    cursor: pointer;
}
.select_payment-item::after {
    content: "";
    position: absolute;
    display: none;
}


.ticket_noPoints-wrap {
    text-align: center;
}
.tiket-text1 {
    margin: 0;
    font-size: 26px;
    text-transform: uppercase;
    margin-top: 20px;   
    margin-bottom: 7px;
}
.tiket-text2 {
    margin: 0;
    margin-bottom: 7px;
    margin-top: 7px;
}
.tiket-text3 {
    margin: 0;
    /* font-size: 24px; */
    text-transform: uppercase;
    font-weight: 500;
    margin-bottom: 7px;
    margin-top: 7px;
}
.tiket-text4 {
    margin: 0;
    margin-bottom: 7px;
    margin-top: 7px;
}
.ticket-text-selected {
    font-weight: 500;
}

.reservationTickets__noContent-text {
    text-align: center;
    font-size: 20px;
    font-weight: 500;
    color: rgb(76, 76, 76);
}


.ticket__login-form {    
    display: flex;
    flex-direction: column;
    margin-top: 30px;
}
.ticket__reserve-btn {
    width: 210px;
    margin: 0;
    padding: 2px;
}

.departure-difference {
    align-self: flex-start;
}

.departure-difference-text {
    margin: 0;
    color: gray;
    font-size: 14px;
}

.reservationTickets__card-header {
    display: flex;
    justify-content: space-between;    
}

.reservationTickets__card-header-left {
    display: flex;
    justify-content: space-between;
    flex: 1;
    cursor: pointer;    
    margin-bottom: 20px;
    padding-right: 5px;
    border-right: 1px solid lightgray;
}

.reservationTickets__card-header-right {
    margin-right: 50px;
    margin-left: 50px;
}

.trip_price {
    margin: 0;
}

@media only screen and (max-width: 780px) {
    .reservationTickets__card-header-right {
        margin-right: 30px;
        margin-left: 30px;
    }
}

@media only screen and (max-width: 600px) {
    .reservationTickets__card-header-right {
        margin-right: 10px;
        margin-left: 10px;
        font-size: 14px;
    }
    .departure-difference-text {
        font-size: 12px;
    }
    .time {
        font-size: 18px;
        font-weight: 500;
    }
    .place {
        font-size: 14px;
    }
    .detail-row{
        font-size: 14px;
    }
    .detail__form-textarea {
        font-size: 14px;
    }
    .reserve-btn{
        font-size: 16px;
    }
}