.admin__header{
    padding-top: 15px;
}

.admin__header-link{
    padding: 10px 30px;
    border-radius: 12px;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    -ms-border-radius: 12px;
    -o-border-radius: 12px;
    cursor: pointer;
}

.admin__main-wrap {
    margin-top: 10px;
}

.admin__main-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50px;
    margin-top: 10px;
    font-size: 18px;
    background-color: #0C8EC8;
    color: #fff;
    border-radius: 10px;
    cursor: pointer;
}