.driver__container.dark {
    
    color: #fff;
}

body:has(.driver__container.dark){
    background-color: black;
}

.driver__reservationTickets__content {
    display: flex;
    flex-direction: column;
}

.driver__card {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    padding: 5px;
    box-shadow: 1px 1px 5px 1px rgb(197, 197, 197);
}

.driver_ticker-selected {
    text-transform: uppercase;
    font-weight: 500;
}

.driver__card h2 {
    align-self: center;
    font-size: 20px;
    font-weight: 400;
    margin: 0;
}

.driver__card:has(.driver__passenger) h2 {
    font-size: 24px;
    font-weight: 600;
    margin: 20px 0;
}

.driver__passenger {
    display: flex;    
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgb(197, 197, 197);
    margin-bottom: 1rem;
}

.driver__passenger-buttons{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.driver__passenger-info {
    max-width: 200px;
}

.handled.driver__passenger-info {
    color: #888989;
}

.handled.driver__passenger-info.negative{
    text-decoration: line-through;
}

.handled.driver__passenger-info p{
    margin: 5px 0;
}

.driver__container-data {
    text-align: center;
    margin: 0;
    font-weight: 500;
    color: #414a4c;
    padding-bottom: 1rem;
}

.driver__passenger-btn {
    display: inline-block;
    padding: 6px 20px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    text-transform: uppercase;
    cursor: pointer;
}

.driver__passenger-info-comment {
    text-decoration: underline;
}

.driver__passenger-btn:hover {
    background-color: #0056b3;
}

.driver__passenger-btn:active {
    background-color: #003080;
}

.driver__all-trips-wrap {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid lightgrey;
    border-top: 1px solid lightgrey;
    margin-bottom: 30px;
    padding-top: 60px;
}

.driver__all-trips-item_title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0 0 30px;
    box-sizing: border-box;
}
.driver__all-trips-btn{
    width: 3px;
    height: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 0 30px;
    cursor: pointer;
}

.driver__all-trips-btn span{
    width: 3px;
    height: 3px;
    border-radius: 50%;
    background-color: black;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.dark .driver__all-trips-btn span{
    background-color: white;
}

.driver__all-trips-item {
    width: 100%;
    font-size: 0.875rem;

}

.driver__all-trips-item_trip{
    background-color: #e8e8e8;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    flex-wrap: wrap;
}

.dark .driver__all-trips-item{
    background-color: #000039;
}

.driver__all-trips-item-sel {
    background-color: #c6e1ff;
    font-size: 1rem;
    font-weight: 600;
}

.dark .driver__all-trips-item-sel {
    background-color: #002550;
}

.driver__all-trips-text {
    text-align: center;
    
}

.driver__all-trips-text-b {
    font-weight: 600;
    font-size: 1rem;
}

.driver__all-trips-count{
    font-size: 0.875em;
    color: #414a4c;
}

.dark .driver__all-trips-count,
.dark .driver__container-data{
    color: #969696;
}

.dark .modal__content{
    color: black;
}

.modal__content {
    max-height: 80vh;
    overflow-y: auto;
}

.modal__content .driver__passenger-info{
    max-width: none;
}

.modal__title{
    text-align: center;
    font-weight: 500;
    font-size: 1.25rem;
}

.modal__title_link{
    text-decoration: underline;
    font-weight: 400;
}

.modal__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
}
.modal__row:not(:last-child) {
    margin-bottom: 1.5rem;
}
