.calendar {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  box-shadow: 2px 2px 2px 2px gray;    
  width: 380px;
}

.calendar-header {    
  display: flex;
  flex-direction: column; 
  padding: 0 0 10px 0;
  background-color: rgb(238, 237, 237);
}

.calendar-header-title {
  display: flex;
  justify-content: center;  
  align-items: center;
  margin-bottom: 10px;
}

.calendar-title {
  margin: 10px;
  font-size: 20px;
  font-weight: 400;
}

.calendar-arrow {
  font-size: 25px;
  cursor: pointer;
}

.days-of-week {
  display: flex;
  justify-content: space-around;
  width: 380px;
}

.calendar-days {
  width: 380px;
}

.calendar-day {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 14%;
}

.calendar-row {
  display: flex;
  justify-content: space-around;
}

.calendar-day {
  padding-top: 15px;
  padding-bottom: 15px;
  cursor: pointer;
}

.calendar-day-disabled {
  color: gray;
}

.clendar-day-selected {
  border-radius: 50%;
  background-color:#0C8EC8;
  color: #fff;
  font-weight: 600;
}


@media only screen and (max-width: 800px) {
  .days-of-week {
    display: flex;
    justify-content: space-around;
    width: 270px;
  }
  
  .calendar-days {
    width: 270px;
  }
  .calendar{
    width: 270px;
  }
  .calendar-day {
    padding-top: 8px;
    padding-bottom: 8px;
  }
  .calendar-title{
    font-size: 18px;
  }
  .days-of-week{
    font-size: 14px;
  }
}
  