.admin__modal_employees-container {
    border-bottom: 1px solid gray;
    margin-bottom: 30px;
}

.admin__modal_employees-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.admin__modal_employees-create {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.admin__modal-employee_input {    
    padding: 5px;    
    font-size: 18px;
    border-radius: 5px;
}

.admin__modal-employee_checkbox-wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}