.contact__content {
    display: flex;
    gap: 10px;
    justify-content: center;
    margin-bottom: 30px; 
}

.contact-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    padding: 10px;
    background-color: #0C8EC8;
    color: #fff;
    border: none;
    cursor: pointer;
    font-size: 16px;
}

.contact-btn-icon {
    margin-right: 5px;
}

.contact-btn:hover {
    background-color: #0e7fb3;  
    color: #fff;  
}

.contact-btn p {
    padding: 0;
    margin: 0;
}

.contact-form {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 50px;    
}

.contact-form-title{
    font-size: 18px;
}