.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer__dispetcherPhone-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer__dispetcherPhone-info {
    text-transform: uppercase;
    font-size: 20px;
    text-align: center;
}

.footer__dispetcherPhone-phone-wrap {
    display: flex;
    gap: 25px;
    margin-bottom: 20px;
}

.footer_dispetcherPhone-phone {
    font-size: 32px;
}

.footer__about {
    margin-bottom: 20px;
}

.footer__about-text {
    text-align: center;
    font-size: 14px;
}

@media only screen and (max-width: 800px) {
    .footer__dispetcherPhone-info {
        font-size: 16px;
    }
    .footer_dispetcherPhone-phone {
        font-size: 24px;
    }
    .footer__about-text {        
        font-size: 12px;
        font-weight: 300;
    }
}