.info__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.info__content p {
    margin: 0;
}

.info__text-red {
    color: red;
    text-align: center;
}

.info__text-darkred {
    font-weight: 700;
    color: rgb(196, 22, 22);
}

.info__main {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
}

.info__main-name {
    font-size: 32px;
    text-transform: uppercase;
    font-weight: 400;
}

.info__main-description {
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    color: gray;
}

@media only screen and (max-width: 600px) {
    .info__main-name {
        font-size: 24px;
        margin: 0;
    }
    .info__main-description {
        font-size: 16px;
    }
}