.header{
    display: flex;
    justify-content: space-between;
    padding: 1rem;
}
.btn{
    font-weight: 400;
    font-size: 0.875rem;
    line-height: 2;
    padding: 4px 7px;
    border-radius: 7px;
    text-transform: uppercase;
    background-color: #0C8EC8;
    color: #fff;
    cursor: pointer;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    -ms-border-radius: 7px;
    -o-border-radius: 7px;
    text-align: center;
}

.search{
    padding: 1rem;
    border-bottom: 1px solid #0C8EC8;
}

.search input{
margin-bottom: 0.5rem;
padding: 5px;
}

.results{
    padding: 1rem;
}

.userInfo{
    padding-bottom: 1rem;
    border-bottom: 1px solid #0C8EC8;
}

.result{
    padding-top: 1rem;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid #0C8EC8;
}

.delete{
    background-color: rgb(92, 14, 14);
}

.edit{
    margin-bottom: 1rem;
    background-color: rgb(199, 188, 24);
}

.modalRow{
    display: flex;
    gap: 0.5rem;
    margin-bottom: 1rem;
}

.modalRow textarea{
    height: 40px;
    resize: none;
}

.modalRow textarea,
.modalRow select,
.modalRow input{
    flex: 1 1 auto;
    padding: 5px;
}

