.admin__modal_trip-content {
    padding-bottom: 15px;
    margin-bottom: 25px;
    border-bottom: 1px solid gray;
}

.admin__modal_trip-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;   
    border-bottom:1px solid gray;
}
.admin__modal_trip-text{
    cursor: pointer;
}
.admin__modal_trip-text:hover{
    background-color: #9ce0ff;
}

.admin__modal_trip-row p {
    margin: 0;    
}

.admin__trip_modal-input {
    width: 50px;
}

.mb15 {
    margin-bottom: 15px;
}

.admin__modal_trip-info {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid black;
}

.admin__modal_trip-user {
    margin-bottom: 10px;
    background-color: #cef0ff;
}