.direction-btn-selected {
  background-color: #0C8EC8;
  color: #fff;
}

.reservation__content {
    display: flex;
    flex-direction: column;    
    gap: 10px;
    margin-bottom: 100px;
}

.direction-text {
    align-self: center;
    text-transform: uppercase;
    font-size: 18px;
    font-weight: 600;
    margin-top: 20px;
}

.direction-btn__wrap {
    display: flex;
    text-transform: uppercase;
}

.direction-btn {
    width: 50%;
    font-size: 30px;
    text-align: center;
    box-shadow: 0px 1px 3px 2px lightgray;
    padding: 25px 0px;
    margin-bottom: 10px;
    cursor: pointer;
}

.direction-btn-left {
    border-radius: 20px 0px 0px 20px;
}
.direction-btn-right {
    border-radius: 0px 20px 20px 0px;
}

.additional__wrap {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.additional__container {
  box-shadow: 0px 1px 3px 2px lightgray;
  padding: 5px 22px;
  border-radius: 5px;    
  cursor: pointer;
}

.additional_title {
    color: rgb(76, 76, 76);
    text-transform: uppercase;
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 10px;
}

.additional-value {
    font-size: 22px;
}

.additional_title-seats {
    text-align: center;
}

.seats {
    display: flex;
    justify-content: space-around;
    flex-grow: 1;
}

.seat {
    flex: 1;
    text-align: center;
    padding: 10px 0px;
    border: 1px solid rgb(186, 186, 186);
    cursor: pointer;
    font-size: 18px;
}

.seat-selected {
  background-color: #0C8EC8;
  color: #fff;
}




  .pointer {
    cursor: pointer;
  }
  

.places__form {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  /* align-items: center;     */
  background-color: #fff;
  box-shadow: 1px 1px 5px 3px gray;
  font-size: 24px;
}

.places__form-question {
  padding: 10px 12px;
  box-shadow: 0px 2px 2px gray;
}

.places__form-paragraph {
  padding: 5px 10px;
}

.places__form-paragraph {
  padding: 5px 10px;
}

.places__form-paragraph:hover {
  background-color: rgb(205, 205, 205);
  cursor: pointer;
}


@media only screen and (max-width: 800px) {
  .places__form-question {
    font-weight: 600;
  }
  .places__form {
    width: 100%;
    height: 100%;
  }

  .direction-btn__wrap {
    flex-direction: column;
  }  

  .direction-btn {
    width: 100%;
    font-size: 20px;
    padding: 10px;
    box-sizing: border-box;
  }

  .direction-btn-left {
    border-radius: 20px 20px 0 0;
    margin: 0;
  }
  .direction-btn-right {
    border-radius: 0 0 20px 20px;
  }

  .additional_title {
    font-size: 16px;
    font-weight: 500;
  }
  .additional-value{
    font-size: 16px;
  }
  .additional_title-seats{
    font-size: 14px;
    font-weight: 400;
  }
  .seat {
    font-size: 14px;
  }
  .contact-btn {
    font-size: 14px;
  }
}

@media only screen and (max-width: 600px) {
  .direction-text {
    font-size: 16px;
    margin-top: 0px;
  }

}