.admin__modal-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100dvw;
    height: 100dvh;
    background-color: rgb(255, 255, 255);
    display: flex;
    align-items: center;
    justify-content: center;
}

.admin__modal-container {
    position: relative;
    display: flex;
    flex-direction: column;
    /* position: absolute;    
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%; */
    /* max-width: 600px;
    height: 400px; */
    width: 90vw;
    height: 80vh;
    border-radius: 10px;
    box-shadow: 0px 1px 3px 2px lightgray;
    background-color: #fff;
    padding: 30px 10px;
    overflow-y: auto;
}




.admin__modal-close {    
    position: absolute;
    top: 15px;
    left: 50%;
    background: url(../../../images/close.png)
    no-repeat center;
    background-size: cover;   
    min-height: 30px;
    min-width: 30px;
    align-self: center;
    cursor: pointer;
    margin-bottom: 10px;
}

.admin___modal-change {
    border-bottom: 1px solid gray;
    padding-bottom: 10px;
}
.no-border {
    border: none;
}

.admin___modal-wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.admin___modal-input_wrap {
    display: flex;
    width: 100%;
}

.admin__modal-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 30px;
    min-width: 30px;
    border-radius: 5px;    
    cursor: pointer;
    transition: all 0.1s;    
}

.admin__modal-success {
    border: 2px solid #00FF00;    
}

.admin__modal-danger {
    border: 2px solid #FF0000;
    margin-right: 5px;
    margin-left: 10px;    
}

.admin__modal-success:active {
    background-color: #00FF00;
}

.admin__modal-danger:active {
    background-color: #FF0000;
}

.admin__modal-checkbox_text {
    margin: 0;
}

.admin_modal-input {        
    padding: 5px;    
    width: 100%;
    max-width: 400px;
    font-size: 18px;
    border-radius: 5px;
}

.admin_modal-input-short {
    max-width: 40px;
}


.admin__modal-checkbox {
    display: flex;
    align-items: center;
}

.admin___modal-add {
    margin-top: 20px;
}

.admin___modal-title {
    color: rgb(81, 81, 81);
    text-align: center;
}

