.page__error {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.page__error-message-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.page__error-title {
    font-size: 150px;
    color: gray;
    margin: 0;
}

.page__error-message {
    color: gray;
}

.page__error-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 120px;
    height: 40px;
    color: #333333;
    background-color: #FFD700;
    text-transform: uppercase;
    border-radius: 10px;
    cursor: pointer;
}